import React, { useEffect, useState } from "react";
import Loader from "../../../common/loader/Loader";
import { Select as AntSelect, message } from "antd";
import {
  getRetailersForASMZSM,
  getBankTransferforASMASM,
  enquiryBankTransferforASMASM,
  getBankTransferExcelReportforASMASM,
} from "../../../api/login/Login";
import {
  Pagination,
  Autocomplete,
  Box,
  TextField,
  Typography,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from "@mui/material";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FaEye } from "react-icons/fa";
import Modal from "react-bootstrap/Modal";
import Table from "react-bootstrap/Table";
import * as XLSX from 'xlsx'; 
import { useReportDates } from "../../../utils/getDate";
const AsmBankTransferReport = () => {
  const today = new Date().toISOString().split("T")[0];
 const { startDate, endDate } = useReportDates();
  const [loading, setLoading] = useState(false);
  const [selectedRow, setSelectedRow] = useState(null);
  const [page, setPage] = useState(1);
  const [retailers, setRetailers] = useState([]);
  const [selectedRetailer, setSelectedRetailer] = useState(null);
  const [totalCount, setTotalCount] = useState(0);
  const [submit, setSubmit] = useState(false);
  const [data, setData] = useState([]);

  const [rowsPerPage, setRowsPerPage] = useState(10);

  const [filter, setFilter] = useState({
    customer_mobile: "",
    response: "",
    start_date: startDate,
    end_date: endDate,
    txn_id: "",
    batch_id: "",
    bank: "",
    addhar_no: "",
  });

  const statusOptions = [
    { value: "", label: "All" },
    { value: "1", label: "Pending" },
    { value: "2", label: "Success" },
    { value: "3", label: "Failed" },
    { value: "4", label: "Refunded" },
  ];

  const bankOptions = [
    { value: "", label: "All" },
    { value: "bank1", label: "Bank 1" },
    { value: "bank2", label: "Bank 2" },
  ];

  const getRetailers = async () => {
    try {
      let res = await getRetailersForASMZSM();
      if (res?.data?.error === false) {
        setRetailers(res.data?.data || []);
      } else {
        setRetailers([]);
      }
    } catch (err) {
      console.error(err);
      setRetailers([]);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFilter((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleStatusChange = (value, key) => {
    setFilter((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const handleSearch = () => {
    setPage(1)
    setSubmit((prev) => !prev);
  };

  const ResetData = () => {
    setFilter({
      customer_mobile: "",
      response: "",
      start_date: "",
      end_date: "",
      txn_id: "",
      batch_id: "",
      bank: "",
      addhar_no: "",
    });
    setSelectedRetailer(null);
    setSubmit((prev) => !prev);
    setRowsPerPage(10);
    setPage(1)
  };

  const handleChangeRowsPerPage = (e) => {
    setRowsPerPage(e.target.value);
    setPage(1);
  };

  const handleChangePage = (event, value) => {
    setPage(value);
  };

  const handleViewClick = (item) => {
    setSelectedRow(item);
  };

  useEffect(() => {
    getRetailers();
  }, []);

  const getData = async () => {
    setLoading(true);
    try {
      const res = await getBankTransferforASMASM({
        ...filter,
        retailer_id: selectedRetailer?._id || "",
        page,
        limit: rowsPerPage,
      });
      if (res?.data?.error === false) {
        setData(res.data?.data?.dmtTxns || []);
        setTotalCount(res.data?.data?.totalTxns || 0);
      } else {
        setData([]);
        setTotalCount(0);
        message.error(res.data?.message || "Something went wrong");
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
  }, [submit, page, rowsPerPage]);

  const responseEnquiry = async (id) => {
    setLoading(true);
    try {
      let res = await enquiryBankTransferforASMASM(id);
      if (res?.data?.error === false) {
        message.success(res.data?.message);
        setSubmit((prev) => !prev);
      } else {
        message.error(res.data?.message);
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

    const downloadExcel = async () => {
      try {
        setLoading(true);
        const res = await getBankTransferExcelReportforASMASM({
          ...filter,
          retailer_id: selectedRetailer?._id || "",
        });
  
        if (res?.data?.data?.dmtTxns?.length > 0) {
          const dataForExcel = res?.data?.data?.dmtTxns?.map((item, i) => ({
            "Sr. No": i + 1,
            "Date / Time": item?.createdAt || "",
            "Transaction ID": item?.txn_id || "",
            "Batch ID": item?.batchId || "",
            "Transaction Mode": item?.txn_mode || "",
            "Amount": item?.amount || "",
            "Charges": item?.charges || "",
            "TDS": item?.tds || "",
            "Commission": item?.commission || "",
            "Distributor Commission": item?.distributor_commission || "",
            "Super Distributor Commission": item?.master_dist_commission || "",
            "Opening Balance": item?.o_bal || "",
            "Closing Balance": item?.c_bal || "",
            "Status": (
              item?.response === 2 ? "Success" :
              item?.response === 1 ? "Pending" :
              item?.response === 3 ? "Failed" :
              item?.response === 4 ? "Refund" :
              "Unknown"
            ),
            "UTR": item?.utr || "",
            "Beneficiary Name": item?.beneficiary_name || "",
            "Customer Name": item?.customer_name || "",
            "Customer Mobile": item?.customer_mobile || "",
            "Bank Name": item?.bank_name || "",
            "IFSC Code": item?.ifsc_code || "",
            "Account Number": item?.account_number || "",
            "Retailer Name": item?.retailer_name || "",
            "Retailer Mobile": item?.retailer_mobile || "",
            "Retailer Refer ID": item?.retailer_refer_id || "",
            "Created By Name": item?.CreatedBy_name || "",
            "Created By Mobile": item?.CreatedBy_mobile || "",
            "Created By Refer ID": item?.CreatedBy_refer_id || "",
            "Status Update Time": item?.status_update_time || ""
          }));
          
            
  
          const ws = XLSX.utils.json_to_sheet(dataForExcel);
          const wb = XLSX.utils.book_new();
          XLSX.utils.book_append_sheet(wb, ws, "Bank Transfer Report");
          XLSX.writeFile(wb, `BankTransfer_Report_${new Date().toLocaleDateString()}.xlsx`);
        } else {
          message.error('No data available to export!');
        }
      } catch (error) {
        message.error('Failed to download Excel. Please try again.');
      } finally {
        setLoading(false);
      }
    };

  const totalPages = Math.ceil(totalCount / rowsPerPage);

  return (
    <>
      {loading && <Loader />}
      <div className="card">
        <div className="card-body">
          <form>
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  name="start_date"
                  className="form-control"
                  max={today}
                  value={filter.start_date}
                  onChange={handleChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  name="end_date"
                  className="form-control"
                  max={today}
                  value={filter.end_date}
                  onChange={handleChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Customer Mobile</label>
                <input
                  type="number"
                  name="customer_mobile"
                  className="form-control"
                  placeholder="Enter Customer Mobile"
                  value={filter.customer_mobile}
                  onChange={handleChange}
                />
              </div>
              {/* <div className="form-group col-md-4">
                <label>Aadhaar Number</label>
                <input
                  type="number"
                  name="addhar_no"
                  className="form-control"
                  placeholder="Enter Aadhaar Number"
                  value={filter.addhar_no}
                  onChange={handleChange}
                />
              </div> */}
              <div className="form-group col-md-4">
                <label>Txn Id</label>
                <input
                  type="text"
                  name="txn_id"
                  className="form-control"
                  placeholder="Enter Txn Id"
                  value={filter.txn_id}
                  onChange={handleChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Batch Id</label>
                <input
                  type="text"
                  name="batch_id"
                  className="form-control"
                  placeholder="Enter Batch Id"
                  value={filter.batch_id}
                  onChange={handleChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Bank</label>
                <AntSelect
                  style={{ width: "100%" }}
                  value={filter.bank}
                  onChange={(value) => handleStatusChange(value, "bank")}
                  options={bankOptions}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Status</label>
                <AntSelect
                  style={{ width: "100%" }}
                  value={filter.response}
                  onChange={(value) => handleStatusChange(value, "response")}
                  options={statusOptions}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Select Retailer</label>
                <Autocomplete
                  options={retailers}
                  getOptionLabel={(option) => option.name || ""}
                  value={selectedRetailer ?? null}
                  size="small"
                  onChange={(e, newVal) => setSelectedRetailer(newVal)}
                  renderOption={(props, option) => (
                    <li {...props} key={option.retailer_id}>
                      <Box sx={{ fontSize: "13px", color: "var(--bs-nav)" }}>
                        {option.name}
                      </Box>
                    </li>
                  )}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Select Retailer"
                      InputLabelProps={{
                        sx: {
                          "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                            top: "-3px",
                          },
                        },
                      }}
                      sx={{
                        "& label.Mui-focused": {
                          color: 'var(--bs-nav)',
                        },
                        "& .MuiInputBase-root": { backgroundColor: "#fff" },
                        "& .MuiOutlinedInput-root": {
                          "&:hover fieldset": {
                            borderColor: "var(--bs-primary1)",
                          },
                          "&.Mui-focused fieldset": {
                            borderColor: "var(--bs-primary1)",
                          },
                        },
                      }}
                    />
                  )}
                />
              </div>
              <div className="form-group col-md-12">
                <button
                  type="button"
                  className="btn btn-primary mr-3"
                  onClick={handleSearch}
                >
                  Search
                </button>
                <button
                  type="button"
                  className="btn btn-light"
                  onClick={ResetData}
                >
                  Reset
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
      <div className="card mt-4" style={{ overflow: "auto" }}>
        <div className="card-body">
          <div id="myTable_wrapper" className="dataTables_wrapper no-footer">
            <div className="d-flex  justify-content-end align-items-center">
              <div className="dataTables_length mb-3" id="myTable_length">
                <Button
                  className="btn btn-success cusxel"
                  onClick={downloadExcel}
                  // disabled={loading}
                >
                  Download Excel sheet
                </Button>
             
              </div>
            </div>
         
            <div className="table-responsive">
              <table
                className="table table-striped table-bordered table-hover display dataTable no-footer dtr-inline collapsed"
                role="grid"
                aria-describedby="myTable_info"
              >
                <thead>
                  <tr role="row">
                    <th className="sorting">S.No</th>
                    <th className="sorting">Txn Date</th>
                    <th className="sorting">Batch ID</th>
                    <th className="sorting">Txn Id</th>
                    <th className="sorting">UTR No </th>
                    <th className="sorting">Bank Name</th>
                    <th className="sorting">Amount</th>
                    <th className="sorting">Txn Status</th>
                    <th className="sorting">View</th>
                    <th className="sorting">Txn. Mode</th>
                    <th className="sorting">Retailer Name</th>
                    <th className="sorting">Retailer Code</th>
                    <th className="sorting">Retailer Number</th>
                    <th className="sorting">Status Update At</th>
                    <th className="sorting">Enquiry</th>
                    <th className="sorting">Invoice</th>
                    <th className="sorting">Dispute</th>
                  </tr>
                </thead>
                <tbody>
                  {data?.map((item, i) => (
                    <tr className="odd" key={item?._id}>
                      <td valign="top" className="dataTables_empty">
                      {i + 1 + ((page-1) * rowsPerPage)}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.createdAt}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.batchId}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.txn_id || "---"}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.utr}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.bank}
                      </td>

                      <td valign="top" className="dataTables_empty">
                        {item?.amount}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.response == 1 ? (
                          <button
                            className="btn btn-warning"
                            style={{ width: "130px" }}
                          >
                            Pending
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.response == 2 ? (
                          <button
                            className="btn btn-success"
                            style={{ width: "130px" }}
                          >
                            Success
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.response == 3 ? (
                          <button
                            className="btn btn-danger"
                            style={{ width: "130px" }}
                          >
                            Failed
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.response == 4 ? (
                          <button
                            className="btn btn-light"
                            style={{ width: "130px" }}
                          >
                            Refunded
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.response == 5 ? (
                          <button
                            className="btn btn-light"
                            style={{ width: "130px" }}
                          >
                            Not Initiated
                          </button>
                        ) : (
                          ""
                        )}
                      </td>
                      <td valign="top" className="dataTables_empty text-center">
                        <FaEye
                          className="eyeiconreport"
                          onClick={() => handleViewClick(item)}
                        />
                      </td>

                      <td valign="top" className="dataTables_empty">
                        {item?.txn_mode == "1"
                          ? "NEFT"
                          : item?.txn_mode == "2"
                          ? "IMPS"
                          : item?.txn_mode}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_name}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_refer_id}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_mobile}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.status_update_time}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        <Button
                          onClick={() => responseEnquiry(item?._id)}
                          disabled={
                            loading ||
                            (item?.response == 1 && item?.tx_status == 0) ||
                            item?.response == 2 ||
                            item?.response == 4 ||
                            item?.response == 5
                          }
                        >
                          Enquiry
                        </Button>
                      </td>
                      <td valign="top" className="dataTables_empty">
                        <Link
                          className="btn btn-primary"
                          to={`/details/${item?.batchId}`}
                        >
                          Invoice
                        </Link>
                      </td>
                      <td valign="top" className="dataTables_empty">
                        <Link
                          className="btn btn-primary"
                          to={`/add-ticket/${item?._id}`}
                          state={{
                            item,
                            serviceId: "65f9484a26eb74e182c640fc",
                          }}
                        >
                            <span style={{color:"#fff"}}>Dispute</span>
                        </Link>
                      </td>
                    </tr>
                  ))}

                  {data?.length === 0 && (
                    <td
                      style={{ textAlign: "center" }}
                      colSpan={14}
                      valign="top"
                      className="dataTables_empty"
                    >
                      No Record Found
                    </td>
                  )}
                </tbody>
              </table>
            </div>
          </div>
          {/* </div> */}
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mt={2}
            flexWrap="wrap"
            gap={2}
          >
            <Box display="flex" alignItems="center" gap={2}>
              <FormControl size="small" sx={{ width: "150px" }}>
                <InputLabel>Rows per page</InputLabel>
                <Select
                  value={rowsPerPage}
                  onChange={handleChangeRowsPerPage}
                  label="Rows per page"
                  sx={{
                    fontSize: "14px",
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#ccc",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                  }}
                >
                  {[5, 10, 25, 50].map((num) => (
                    <MenuItem key={num} value={num}>
                      {num}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <Typography
                variant="body2"
                sx={{ fontSize: "14px", color: "#555" }}
              >
                Total Entries: {totalCount}
              </Typography>
            </Box>
            <Pagination
              count={totalPages}
              page={page}
              onChange={handleChangePage}
              shape="rounded"
              showFirstButton
              showLastButton
              sx={{
                "& .MuiPaginationItem-root": {
                  color: "var(--bs-primary1)",
                  borderRadius: "8px",
                },
                "& .MuiPaginationItem-root.Mui-selected": {
                  backgroundColor: "var(--bs-primary1)",
                  color: "#fff",
                  fontWeight: "bold",
                  "&:hover": { backgroundColor: "var(--bs-primary1)" },
                },
              }}
            />
          </Box>
        </div>
      </div>

      {/* ///Show PopUP */}
      {selectedRow && (
        <Modal
          show={!!selectedRow}
          onHide={() => setSelectedRow(null)}
          centered
          className="transaction-modal"
        >
          <Modal.Header closeButton className="bg-primary text-white">
            <Modal.Title>Transaction ID # {selectedRow.txn_id}</Modal.Title>
          </Modal.Header>
          <Modal.Body className="bg-white">
            <Table borderless style={{ border: "none" }}>
              <tbody>
                <tr>
                  <td className="trportpopup">Beneficiary Name:</td>
                  <td className="trportpopup">
                    {selectedRow?.beneficiary_name}
                  </td>
                </tr>
                <tr>
                  <td className="trportpopup">Account No:</td>
                  <td className="trportpopup">{selectedRow?.account_number}</td>
                </tr>
                <tr>
                  <td className="trportpopup">Transaction Date:</td>
                  <td className="trportpopup">
                    {selectedRow?.createdAt.split(" ")[0]}
                  </td>
                </tr>
                <tr>
                  <td className="trportpopup">Bank Name:</td>
                  <td className="trportpopup">{selectedRow?.bank_name}</td>
                </tr>
                <tr>
                  <td className="trportpopup">Beneficiary Name:</td>
                  <td className="trportpopup">
                    {selectedRow?.beneficiary_name}
                  </td>
                </tr>
                <tr>
                  <td className="trportpopup">IFSC Code:</td>
                  <td className="trportpopup">{selectedRow?.ifsc_code}</td>
                </tr>
                <tr>
                  <td className="trportpopup">Remitter Mobile No:</td>
                  <td className="trportpopup">
                    {selectedRow?.customer_mobile}
                  </td>
                </tr>

                <tr>
                  <td className="trportpopup">Amount:</td>
                  <td className="trportpopup">₹{selectedRow?.amount}</td>
                </tr>
                <tr>
                  <td className="trportpopup">Charges:</td>
                  <td className="trportpopup">₹{selectedRow?.charges}</td>
                </tr>
                <tr>
                  <td className="trportpopup">Commissions:</td>
                  <td className="trportpopup">₹{selectedRow?.commission}</td>
                </tr>
                <tr>
                  <td className="trportpopup">TDS:</td>
                  <td className="trportpopup">₹{selectedRow?.tds}</td>
                </tr>
                <tr>
                  <td className="trportpopup">UTR No/RRN No:</td>
                  <td className="trportpopup">{selectedRow?.utr}</td>
                </tr>
                <tr>
                  <td className="trportpopup">Opening Balance:</td>
                  <td className="trportpopup">{selectedRow?.o_bal}</td>
                </tr>
                <tr>
                  <td className="trportpopup">Closing Balance:</td>
                  <td className="trportpopup">{selectedRow?.c_bal}</td>
                </tr>

                <tr>
                  <td className="trportpopup">Status:</td>
                  <td className="trportpopup">
                    {selectedRow?.response == 1 && (
                      <span
                        className="badge bg-warning"
                        style={{ fontSize: "12px" }}
                      >
                        Pending
                      </span>
                    )}
                    {selectedRow?.response == 2 && (
                      <span
                        className="badge bg-success"
                        style={{ fontSize: "12px" }}
                      >
                        Success
                      </span>
                    )}
                    {selectedRow?.response == 3 && (
                      <span
                        className="badge bg-danger"
                        style={{ fontSize: "12px" }}
                      >
                        Failed
                      </span>
                    )}
                    {selectedRow?.response === 4 && (
                      <span
                        className=""
                        style={{
                          fontSize: "12px",
                          background: "#e9ecef",
                          padding: "1%",
                        }}
                      >
                        Refunded
                      </span>
                    )}
                  </td>
                </tr>
              </tbody>
            </Table>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setSelectedRow(null)}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      )}
    </>
  );
};

export default AsmBankTransferReport;
